import { json, type LoaderFunctionArgs, type ActionFunctionArgs } from "@remix-run/node";
import { useLoaderData, Form, useActionData } from "@remix-run/react";
import {
  Page,
  Layout,
  Card,
  FormLayout,
  TextField,
  Button,
  Banner,
  Text,
  Divider,
  InlineStack
} from "@shopify/polaris";
import { useState, useCallback } from "react";

export async function loader({ request }: LoaderFunctionArgs) {
  const url = new URL(request.url);
  const shop = url.searchParams.get("shop");
  
  if (!shop) {
    throw new Response("Shop parameter required", { status: 400 });
  }

  // TODO: Load actual branding settings from database
  const brandingSettings = {
    brandName: shop.split('.')[0], // Default to shop name
    primaryColor: "#007C3B", // Shopify green default
    logoUrl: "",
    tagline: "Your mobile shopping experience"
  };

  return json({
    shop,
    brandingSettings
  });
}

export async function action({ request }: ActionFunctionArgs) {
  const formData = await request.formData();
  const url = new URL(request.url);
  const shop = url.searchParams.get("shop");
  
  if (!shop) {
    throw new Response("Shop parameter required", { status: 400 });
  }

  const brandName = formData.get("brandName") as string;
  const primaryColor = formData.get("primaryColor") as string;
  const logoUrl = formData.get("logoUrl") as string;
  const tagline = formData.get("tagline") as string;

  // TODO: Save to database via shop registry
  console.log("Saving branding settings:", { shop, brandName, primaryColor, logoUrl, tagline });

  return json({
    success: true,
    message: "Branding settings saved successfully!"
  });
}

export default function AdminBranding() {
  const { shop, brandingSettings } = useLoaderData<typeof loader>();
  const actionData = useActionData<typeof action>();
  
  const [brandName, setBrandName] = useState(brandingSettings.brandName);
  const [primaryColor, setPrimaryColor] = useState(brandingSettings.primaryColor);
  const [logoUrl, setLogoUrl] = useState(brandingSettings.logoUrl);
  const [tagline, setTagline] = useState(brandingSettings.tagline);

  const handleBrandNameChange = useCallback((value: string) => setBrandName(value), []);
  const handleLogoUrlChange = useCallback((value: string) => setLogoUrl(value), []);
  const handleTaglineChange = useCallback((value: string) => setTagline(value), []);

  return (
    <Page 
      title="Branding Configuration"
      subtitle="Customize your mobile app appearance"
      backAction={{
        url: '/admin'
      }}
    >
      <Layout>
        <Layout.Section>
          {actionData?.success && (
            <Banner tone="success" onDismiss={() => {}}>
              {actionData.message}
            </Banner>
          )}
          
          <Card>
            <Form method="post">
              <FormLayout>
                <Text variant="headingSm" as="h3">Basic Information</Text>
                
                <TextField
                  label="App Name"
                  value={brandName}
                  onChange={handleBrandNameChange}
                  name="brandName"
                  helpText="This will be the name of your mobile app"
                  autoComplete="off"
                />

                <TextField
                  label="Tagline"
                  value={tagline}
                  onChange={handleTaglineChange}
                  name="tagline"
                  helpText="A short description for your mobile app"
                  autoComplete="off"
                />

                <Divider />

                <Text variant="headingSm" as="h3">Visual Design</Text>

                <TextField
                  label="Primary Color"
                  value={primaryColor}
                  onChange={setPrimaryColor}
                  name="primaryColor"
                  helpText="Hex color code for your app's primary color"
                  autoComplete="off"
                  prefix="#"
                  placeholder="007C3B"
                />

                <TextField
                  label="Logo URL"
                  value={logoUrl}
                  onChange={handleLogoUrlChange}
                  name="logoUrl"
                  helpText="URL to your app logo (optional)"
                  autoComplete="off"
                />

                <InlineStack align="end">
                  <Button variant="primary" submit>
                    Save Settings
                  </Button>
                </InlineStack>
              </FormLayout>
            </Form>
          </Card>
        </Layout.Section>

        <Layout.Section variant="oneThird">
          <Card>
            <div style={{ padding: '20px' }}>
              <Text variant="headingSm" as="h3">Preview</Text>
              <div style={{ marginTop: '16px' }}>
                <div 
                  style={{
                    padding: '20px',
                    backgroundColor: primaryColor,
                    color: 'white',
                    borderRadius: '8px',
                    textAlign: 'center'
                  }}
                >
                  <Text variant="headingLg" as="h2">
                    {brandName || 'Your App Name'}
                  </Text>
                  <Text variant="bodyMd" as="p">
                    {tagline || 'Your tagline here'}
                  </Text>
                </div>
              </div>
            </div>
          </Card>
        </Layout.Section>
      </Layout>
    </Page>
  );
}